<template>
  <div class="functionlist tableImage px-4 py-2">
    <v-row
      class="pa-2"
      appear
      tag="transition-group"
      @before-enter="beforeEnter"
      @enter="enter"
    >
      <v-col
        class="align-center pa-0 py-1"
        cols="6"
        md="3"
        v-for="(item, index) in items"
        :key="item.label"
        v-ripple
        :data-index="index"
      >
        <v-alert
          outlined
          :color="item.color"
          border="left"
          class="white ma-0 py-2"
          v-if="item.keyName != 'exit'"
        >
          <!-- <router-link :to="item.route" tag="div">
            <h2 class>{{ item.label }}</h2>
            <v-img :src="item.image" aspect-ratio="2.5" contain></v-img>
          </router-link> -->

          <router-link :to="item.route" custom v-slot="{ navigate }">
            <div @click="navigate" @keypress.enter="navigate" role="link">
              <h2 class>{{ item.label }}</h2>
              <v-img :src="item.image" aspect-ratio="2.5" contain></v-img>
            </div>
          </router-link>
        </v-alert>

        <v-alert
          outlined
          :color="item.color"
          border="left"
          class="white ma-0 py-2"
          v-if="item.keyName == 'exit'"
          @click="logout"
        >
          <h2 class>{{ item.label }}</h2>
          <v-img :src="item.image" aspect-ratio="2.5" contain></v-img>
        </v-alert>
      </v-col>
    </v-row>
    <!-- <pre>
      {{items}}
    </pre> -->
  </div>
</template>

<script>
import gsap from "gsap";

export default {
  computed: {
    items() {
      return this.$store.state.authKeys.filter(
        (item) =>
          this.$store.state.pData[item.authKey] == "true" ||
          item.authKey == "exit_key"
      );
    },
  },

  methods: {
    logout() {
      // this.$store.commit("logined", false);
      // this.$router.push("/login/"+sessionStorage.getItem('dName'));
      this.$router.push("/login");
      sessionStorage.clear(); //清除sessionStorage
    },

    //gsap 動畫
    beforeEnter(el) {
      // console.log('beforeEnter')
      el.style.opacity = 0;
      el.style.transform = "translateY(100px)";
    },
    enter(el, done) {
      // console.log('enter')
      gsap.to(el, {
        opacity: 1,
        y: 0,
        duration: 0.8,
        onComplete: done,
        delay: el.dataset.index * 0.1,
      });
    },
  },
};
</script>
